.dish-inputs {
  position: relative;
  &_container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }
  &_inputs {
    width: 80%;
    margin: 0 auto;
  }
  &_result {
    text-align: center;
    color: #f1f3f5;
    font-size: 9.5rem;
  }
  &_btn {
    width: 80%;
    margin: 0 auto;
    font-size: 32px;
    height: 1.2em;
    background-color: #f1f3f5;
    box-shadow: 4px 2px 0 2px #bac8ff;
    border-radius: 7px;
    border: 0;
  }
}

@media screen and (max-width: 1200px) {
  .dish-inputs_result {
    width: 100%;
    margin: 0 0.5rem;
  }
}
