.picked {
  background-color: #ffc9c9 !important;
  box-shadow: 0.2em 0.2em 0.1em #ff8787 !important;
}

.sample {
  width: 1em;
  height: 1em;
  margin-right: 1em;
}
.menu-container {
  position: relative;
  display: flex;
  flex-direction: column;
  color: #f1f3f5;

  .menus {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 0.8em;
    margin-top: 1rem;
    .menu {
      padding: 0.5em;
      background-color: #bac8ff;
      border-radius: 7px;
      border: 0;
      box-shadow: 0.2em 0.2em 0.1em #748ffc;
    }
  }
  .menu-guide {
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
  }
}

@media screen and (max-width: 1200px) {
  .menus {
    margin-bottom: 1rem;
  }
}
